<template>
  <div id="app">
    <Header id="header" title="SwiftUI Calculator" subtitle="A simple and elegant iOS calculator"/>
    
    <DownloadRow id="download" img="App-Icon.png" alt="SwiftUI Calculator" href="https://github.com/DouglasLiuGamer/SwiftUI-Calculator" :blank="true">
      <i class="fab fa-app-store"/> Available as an Xcode project
    </DownloadRow>

    <div id="content">
      <TextBlock title="Introduction">
        <p>
          This is a small project I built when I was learning SwiftUI.
          Since it is mostly for learning purpose, it is not designed to be a powerful calculator with numerous functionalities.
          But I did try to make it not too simple to lose the fun, so I spent many efforts on the UI/UX design.
        </p>

        <p>
          <i>Nobody will pay for this</i>, not even myself, and I currently don't have an extra $99 for the Apple Developer Program,
          so I don't bother putting this on the AppStore. 
          So right now, enjoy your visit :)
        </p>

        <p>
          <ImgBlock src="Full-Screenshots.png" caption="SwiftUI Calculator" width="700"/>
        </p>
      </TextBlock>

      <TextBlock title="Features for Calculation">
        <p>
          This is a <i>calculator</i>, so it does <i>calculation</i>.
          But the built-in iOS calculator has done that, so this needs to be <i>better</i>.
          This application supports full equations,
          which means that you can construct a complex expression and get the result once and for all.
        </p>

        <p>
          You can construct your expression with numbers, basic operators (+, −, ×, ÷), parentheses and the previous result (ANS).
          With the dedicated implemented UI (introduced later), your expression is ensured to be valid
          and you will get the calculated result based on the operator precedence.
        </p>

        <p>
          <ImgBlock src="Equation.png" caption="A full equation is supported." width="600"/>
        </p>

        <p>
          Although the expression must be valid, <i>the result may not</i>
          since you may encounter the "divided by zero" case.
          In this case, you will get an "Error" result and it will not be stored for the next round.
        </p>

        <p>
          <ImgBlock src="Error.png" caption='You may get a "divided by zero" error.' width="600"/>
        </p>
      </TextBlock>

      <TextBlock title="Reactive UI Design">
        <p>
          The word "<i>reactive</i>" here largely means that the UI will react differently based on your input.
          The most significant design is that the buttons of the calculator will react to your expression.
          If pressing the button will invalidate the expression, the button will be disabled in advance.
          Also, you cannot calculate the expression when it is not completed.
          All these ensure that, if you can type it, you can make it. 
        </p>

        <p>
          <ImgBlock src="Buttons.png" caption="The buttons are reactive." width="700"/>
        </p>

        <p>
          Additionally, parentheses support auto-completion, in case you forget how many of them you have typed.
          If valid, you can do the calculation directly and the application will insert the remaining parentheses for you. 
        </p>

        <p>
          <ImgBlock src="Parentheses.png" caption='Parentheses can be auto-completed.' width="600"/>
        </p>

        <p>
          As shown in the screenshots above, this calculator also supports light/dark mode in iOS 13.
          <i>It is great, and I like my choice of colors.</i>
        </p>
      </TextBlock>

      <TextBlock title="Settings & Localization">
        <p>
          By default, results with fractional parts will be reserved with 4 digits.
          You may change this in the settings.
          Also, with the beautiful <i>taptic engine</i> developed by Apple,
          each button pressed will give you haptic feedback.
          If you don't like it, you can also disable this in the settings.
        </p>

        <p>
          Currently, this application supports English and Chinese (both simplified and traditional),
          <i>because these are the languages that I know</i>.
        </p>

        <p>
          <ImgBlock src="Localization.png" caption="Texts are localized." width="600"/>
        </p>
      </TextBlock>

      <FakeRating/>
    </div>

    <Footer id="footer"/>
  </div>
</template>

<script>
import Header from "./components/Header.vue"
import DownloadRow from "./components/DownloadRow.vue"
import TextBlock from "./components/TextBlock.vue"
import ImgBlock from "./components/ImgBlock.vue"
import FakeRating from "./components/FakeRating.vue"
import Footer from "./components/Footer.vue"

export default {
  name: "App",

  components: {
    Header,
    DownloadRow,
    TextBlock,
    ImgBlock,
    FakeRating,
    Footer
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Montserrat|Raleway:800&display=swap');

:root {
  /* color palette */
  --color-bg: #FAFAFA;

  --color-text: #161F28;
  --color-subtext: #4F6D8D;
  --color-comment: #9EA9B0;

  --color-button: #4D93C1;
  --color-button-hover: #3F6A98;
  --color-button-shadow: #4D93C199;
  --color-button-shadow-hover: #3F6A9899;

  --color-img-shadow: #9EA9B099;
}

::selection {
  /* color */
  background-color: #DFE6EF;
}

body {
  /* color */
  background-color: var(--color-bg);
}

#app {
  /* flexbox container */
  display: flex;
  flex-flow: column nowrap;

  /* box model */
  min-height: 100vh;
  max-width: 1144px;
  margin: 0 auto 0 auto;
}

#header {
  /* box model */
  margin: 120px 0 80px 0;
}

#download {
  /* box model */
  margin-bottom: 80px;
}

#content {
  /* flexbox item */
  flex: 1;
}
#content > div {
  /* box model */
  margin-bottom: 40px;
}

#footer {
  /* box model */
  margin-top: 120px;
}

</style>
