<template>
  <div class="container">
    <div class="prompt">
      That's all. <br/>
      What do you think about it?
    </div>

    <div class="stars">
      <i 
        v-for="n in 5" :key="n" 
        :class="score >= n ? 'fas shadow' : 'far'" class="fa-star"
        :style="{ cursor: clicked ? 'auto' : 'pointer' }"
        @mouseenter="enterStar(n)"
        @mouseleave="leaveStar()"
        @click="onStarClick()"
      />
    </div>

    <div class="msg" :style="{ opacity: clicked ? 1 : 0}">
      This is a fake, I can't receive your rating actually. <br/>
      But anyway, thanks!
    </div>
  </div>
</template>

<script>
export default {
  name: "FakeRating",

  data: function() {
    return {
      score: 0,
      clicked: false
    }
  },

  methods: {
    enterStar: function(n) {
      if (!this.clicked) {
        this.score = n;
      }
    },

    leaveStar: function() {
      if (!this.clicked) {
        this.score = 0;
      }
    },

    onStarClick: function() {
      this.clicked = true;
    }
  }
}
</script>

<style scoped>
.container {
  /* alignment */
  text-align: center;

  /* box model */
  margin-top: 96px;
}

.prompt {
  /* font */
  font-family: "Raleway", sans-serif;
  font-size: 1.5em;

  /* color */
  color: var(--color-text);

  /* selection */
  user-select: none;

  /* cursor */
  cursor: default;
}

.stars {
  /* font */
  font-size: 1.75em;

  /* color */
  color: var(--color-button-hover);

  /* box model */
  margin: 16px 0;
}

.msg {
  /* font */
  font-family: "Montserrat", sans-serif;
  line-height: 125%;

  /* color */
  color: var(--color-comment);

  /* selection */
  user-select: none;

  /* cursor */
  cursor: default;

  /* transition */
  transition: opacity 1s;
}

.shadow {
  /* shadow */
  text-shadow: 0 4px 8px var(--color-button-shadow-hover);
}
</style>
